import React, { Component } from "react";
import Drawer, {
  DrawerAppContent,
  DrawerHeader,
  DrawerContent
} from "@material/react-drawer";
import AddCourseDialog from "./components/AddCourseDialog";
import AddScheduleDialog from "./components/AddScheduleDialog";
import CourseList, { CourseListProps } from "./components/CourseList";
import Fab from "@material/react-fab";

import MaterialIcon from "@material/react-material-icon";
import ScheduleSelect from "./components/ScheduleSelect";
import { SchedulesState } from "./redux/types";
import { connect } from "react-redux";

interface AppProps {
  anySchedulesCreated: boolean;
}

class App extends Component<AppProps> {
  state = { addCourseDialogIsOpen: false, addScheduleDialogIsOpen: false };

  constructor(props: any) {
    super(props);
    this.handleClick = this.handleClick.bind(this);
  }
  handleClick(e: any) {
    if (this.props.anySchedulesCreated) {
      this.setState({ addCourseDialogIsOpen: true });
    } else {
      this.setState({ addScheduleDialogIsOpen: true });
    }
    fetch("/api/schedules/1").then((response) => response.json()).then((json) => {
      console.log(json);
    })
  }
  render() {
    const sections = [];
    sections.push({
      section: {
        dept: "CSCE",
        course_num: "470",
        section_num: "501",
        name: "INFO RETRVL",
        term_code: 201831,
        crn: 123456,
        meetings: [],
        ABCDFQ: [50, 20, 10, 0, 0, 20],
        min_credits: 0.0,
        max_credits: 4.0,
        instructor: "Xia Hu"
      }
    });
    const courseListProps: CourseListProps = {
      courses: [
        {
          dept: "CSCE",
          course_num: "121",
          name: "Introduction to Program Design and Concepts",
          sections
        },
        {
          dept: "CSCE",
          course_num: "470",
          name: "Information Retrieval and Storage",
          sections
        }
      ]
    };
    return (
      <div className="drawer-container">
        <Drawer className="drawer">
          <DrawerHeader>
            <ScheduleSelect />
          </DrawerHeader>

          <DrawerContent className="drawer-content">
            <CourseList courses={courseListProps.courses} />
          </DrawerContent>
        </Drawer>
        <DrawerAppContent className="drawer-app-content">
          <Fab
            className="fab"
            icon={<MaterialIcon icon="add" />}
            onClick={this.handleClick}
          />
        </DrawerAppContent>
        <AddScheduleDialog open={this.state.addScheduleDialogIsOpen} />
        <AddCourseDialog open={this.state.addCourseDialogIsOpen} />
      </div>
    );
  }
}

const mapStateToProps = (state: SchedulesState) => ({
  anySchedulesCreated: Array.from(state.schedules.keys()).length !== 0
});

export default connect(mapStateToProps)(App);
