import React from "react";
import List, {
  ListItem,
  ListItemText,
  ListItemMeta
} from "@material/react-list";
import IconButton from "@material/react-icon-button";
import MaterialIcon from "@material/react-material-icon";

import Collapsible from "react-collapsible";
import SectionListItem, { SectionListItemProps } from "./SectionListItem";

export interface CourseListItemProps {
  dept: string;
  course_num: string;
  name: string;
  sections: Array<SectionListItemProps>;
}

export default class CourseListItem extends React.Component<
  CourseListItemProps
> {
  state = { checked: false, indeterminate: false };
  render() {
    const triggerElem = (
      <ListItem className="course-list-item">
        <ListItemText
          primaryText={`${this.props.dept}-${this.props.course_num}`}
          secondaryText={this.props.name}
        />
        <ListItemMeta
          meta={
            <IconButton>
              <MaterialIcon icon="delete" />
            </IconButton>
          }
        />
      </ListItem>
    );

    return (
      <Collapsible trigger={triggerElem} transitionTime={250}>
        <List twoLine className="course-sections">
          {this.props.sections.map(({ section }, i) => {
            return <SectionListItem key={i} section={section} />;
          })}
        </List>
      </Collapsible>
    );
  }
}
