import React from "react";
import List from "@material/react-list";

import CourseListItem, { CourseListItemProps } from "./CourseListItem";

export interface CourseListProps {
  courses: Array<CourseListItemProps>;
}
export default class CourseList extends React.Component<CourseListProps> {
  render() {
    return (
      <List twoLine avatarList>
        {this.props.courses.map((courseProps, i) => {
          const { dept, name, course_num, sections } = courseProps;
          return (
            <CourseListItem
              key={i}
              dept={dept}
              name={name}
              course_num={course_num}
              sections={sections}
            />
          );
        })}
      </List>
    );
  }
}
