export const ADD_SECTION = "ADD_SECTION";
export const REMOVE_SECTION = "REMOVE_SECTION";
export const ADD_COURSE = "ADD_COURSE";
export const REMOVE_COURSE = "REMOVE_COURSE";
export const ADD_SCHEDULE = "ADD_SCHEDULE";
export const REMOVE_SCHEDULE = "REMOVE_SCHEDULE";
export const SELECT_SCHEDULE = "SELECT_SCHEDULE";

export interface Meeting {
  location: string | null;
  meeting_days: string | null;
  start_time: Date | null;
  end_time: Date | null;
  meeting_type: string;
}

export interface Section {
  dept: string;
  course_num: string;
  section_num: string;
  name: string;
  term_code: number;
  crn: number;
  meetings: Array<Meeting>;
  ABCDFQ: Array<number> | null;
  min_credits: number | null;
  max_credits: number | null;
  instructor: string | null;
}

export interface Course {
  dept: string;
  course_num: string;
  name: string;
  prerequisites: string | null;
  corequisites: string | null;
  min_credits: number | null;
  max_credits: number | null;
  description: string | null;
  sections: Array<Section>;
}

export interface Schedule {
  name: string;
  term_code: number;
  courses: Array<Course>;
  selectedCrns: Set<number>;
}

export interface AddSectionAction {
  type: typeof ADD_SECTION;
  payload: number;
}

export interface RemoveSectionAction {
  type: typeof REMOVE_SECTION;
  payload: number;
}

export interface AddCourseAction {
  type: typeof ADD_COURSE;
  payload: Course;
}

export interface RemoveCourseAction {
  type: typeof REMOVE_COURSE;
  payload: Course;
}

export interface AddScheduleAction {
  type: typeof ADD_SCHEDULE;
  payload: {
    name: string; // Schedule name
    term_code: number;
  };
}

export interface RemoveScheduleAction {
  type: typeof REMOVE_SCHEDULE;
  payload: string; // Schedule name
}

export interface SelectScheduleAction {
  type: typeof SELECT_SCHEDULE;
  payload: string;
}

export interface SchedulesState {
  schedules: Map<string, Schedule>;
  currentSchedule: string | null;
}

export type SectionActionTypes = AddSectionAction | RemoveSectionAction;
export type CourseActionTypes = AddCourseAction | RemoveCourseAction;
export type ScheduleActionTypes =
  | AddScheduleAction
  | RemoveScheduleAction
  | SelectScheduleAction;

export type ActionTypes =
  | SectionActionTypes
  | CourseActionTypes
  | ScheduleActionTypes;
