import {
  SchedulesState,
  Schedule,
  ActionTypes,
  ADD_SECTION,
  REMOVE_SECTION,
  ADD_COURSE,
  REMOVE_COURSE,
  ADD_SCHEDULE,
  REMOVE_SCHEDULE,
  SELECT_SCHEDULE
} from "./types";
const initialState: SchedulesState = {
  schedules: new Map<string, Schedule>(),
  currentSchedule: null
};

export function reducer(
  state = initialState,
  action: ActionTypes
): SchedulesState {
  const { currentSchedule } = state;
  if (currentSchedule !== null) {
    const schedule = state.schedules.get(currentSchedule);
    if (schedule) {
      switch (action.type) {
        case ADD_SECTION:
          schedule.selectedCrns.add(action.payload);
          break;
        case REMOVE_SECTION:
          schedule.selectedCrns.delete(action.payload);
          break;
        case ADD_COURSE:
          schedule.courses.push(action.payload);
          break;
        case REMOVE_COURSE:
          schedule.courses.push(action.payload);
          break;
        case REMOVE_SCHEDULE:
          state.schedules.delete(action.payload);
          break;
      }
    } else {
      if (action.type === ADD_SCHEDULE) {
        const newSchedule: Schedule = {
          name: action.payload.name,
          term_code: action.payload.term_code,
          courses: [],
          selectedCrns: new Set<number>()
        };
        state.schedules.set(action.payload.name, newSchedule);
      } else {
        throw Error("The schedule attempting to be accessed does not exist.");
      }
    }
  } else {
    if (action.type === SELECT_SCHEDULE) {
      console.log("Selecting schedule!");
      state.currentSchedule = action.payload;
    }
  }
  return state;
}