import React from "react";
import { ListItem, ListItemText, ListItemGraphic } from "@material/react-list";
import { Section } from "../redux/types";
import { Checkbox } from "@material/react-checkbox";

export interface SectionListItemProps {
  section: Section;
}

interface GradeDistributionProps {
  ABCDFQ: Array<number> | null;
}

const LETTER_MAP = [
  { letter: "A", color: "#00BCD4" },
  { letter: "B", color: "#A5D6A7" },
  { letter: "C", color: "#FFEE58" },
  { letter: "D", color: "#E57373" },
  { letter: "F", color: "#90A4AE" },
  { letter: "Q", color: "#D1C4E9" }
];

const GradeDistribution: React.FunctionComponent<
  GradeDistributionProps
> = props => {
  const { ABCDFQ } = props;
  console.log(ABCDFQ);
  if (ABCDFQ === null) {
    return null;
  } else {
    return (
      <div style={{ marginLeft: "auto", width: "40%" }}>
        {ABCDFQ.map(function(percentage, index) {
          if (percentage === 0) {
            return null;
          } else {
            return (
              <div
                key={index}
                style={{
                  backgroundColor: LETTER_MAP[index].color,
                  width: `${(percentage - 0.1).toFixed(2)}%`,
                  display: "inline-block",
                  border: "1px solid white",
                  boxSizing: "border-box",
                  height: 20
                }}
              />
            );
          }
        })}
      </div>
    );
  }
};

export default class SectionListItem extends React.Component<
  SectionListItemProps
> {
  render() {
    const { section_num, instructor, ABCDFQ } = this.props.section;
    return (
      <ListItem>
        <Checkbox initRipple={() => {}} />
        <ListItemText primaryText={section_num} secondaryText={instructor} />
        <ListItemGraphic graphic={<GradeDistribution ABCDFQ={ABCDFQ} />} />
      </ListItem>
    );
  }
}
